router.beforeEach((to, from, next) => {
  document.title = to.meta.title;
  if (to.matched.some(rec => rec.meta.requiresAuth)) {
    if (!firebase.auth().currentUser) {
      next({
        path: "/login",
        query: {
          redirect: to.fullPath,
        },
      });
    } else {
      // check user
      store.dispatch("setUser", firebase.auth().currentUser);

      if (store.state.user) {
        if (store.state.user.designation != "user" &&
          store.state.user.status == "active"
        ) {
          next();
        } else {

          if (to.path != "/error/user") {
            next({
              path: "/error/user",
              query: {
                redirect: to.fullPath,
              },
            });
          } else {
            next();
          }
        }
      } else {
        next();
      }
    }
  } else if (to.matched.some(rec => rec.meta.requiresGuest)) {
    if (firebase.auth().currentUser) {
      next({
        path: "/",
        query: {
          redirect: to.fullPath,
        },
      });
    } else {
      next();
    }
  } else {
    next();
  }


  if (to.matched.some(rec => rec.meta.requireAdmin)) {
    if (store.state.user) {
      if (store.state.user.designation == "admin") {
        next();
      } else {
        next({
          path: "/error/auth",
          query: {
            redirect: to.fullPath,
          },
        });
      }
    } else {
      next();
    }
  } else if (to.matched.some(rec => rec.meta.requiresAdminOrUploader)) {
    if (store.state.user) {
      if (store.state.user.designation == "admin" || store.state.user.designation == "webappUploader") {
        next();
      } else {
        next({
          path: "/error/auth",
          query: {
            redirect: to.fullPath,
          },
        });
      }
    } else {
      next();
    }
  } else if (to.matched.some(rec => rec.meta.requiresAdminOrDelivererGuy)) {
    if (store.state.user) {
      if (store.state.user.designation == "admin" || store.state.user.designation == "deliverer") {
        next();
      } else {
        next({
          path: "/error/auth",
          query: {
            redirect: to.fullPath,
          },
        });
      }
    } else {
      next();
    }
  } else {
    next();
  }



  if (to.matched.some(rec => rec.meta.checkMaintenance)) {
    let url = domain + "aboutUs";
    axios
      .get(url, {
        params: {
          name: 'webappUnderMaintenance'
        }
      })
      .then(res => {

        let maintenanceMode = true;
        if (res.data == 'false') {
          maintenanceMode = false;
        } else {
          maintenanceMode = true;
        }
        if (maintenanceMode == false) {
          next();
        } else {
          next({
            path: "/under/maintenance",
            query: {
              redirect: to.fullPath,
            },
          });
        }
      })
      .catch(err => {
        console.log(err);
      });

  }


  //
});

export default router;



