                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'name' &&
                  editItemProductId == item.productId
                "
              />
              <span
                @dblclick="editProperty(item.productId, 'name', item.name)"
                v-else
              >
                <i
                  class="fas fa-circle text-success"
