                >
                  <i class="fa fa-check pr-2"></i>Save
                </button>
              </div>
            </td>
            <td class="font-weight-medium" v-else>
              <span
                @dblclick="editProperty(item.productId, 'cocat', item.itemId)"
              >
                {{ item.catName }}
              </span>
            </td>
            <td
              class="font-weight-medium"
              v-if="
                !(
                  editItemProperty == 'cocat' &&
                  editItemProductId == item.productId
                )
              "
            >
              <span
                @dblclick="editProperty(item.productId, 'cocat', item.itemId)"
              >
                {{ item.subcatName }}
              </span>
            </td>
            <td
              class="font-weight-medium"
              v-if="
                !(
                  editItemProperty == 'cocat' &&
