</template>
<script>
import axios from "axios";
import Vue from "vue";
import _ from "lodash";
import $ from "jquery";
import firebase from "firebase";
import db from "../../firebaseInit";
import { bus } from "../../../main";
import { firestorage } from "../../firebaseInit";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith,
} from "@/store/variable";
import store from "@/store/store";
import Loader from "../../Loader";

import { inventoryDataMixin } from "../../Mixins/Inventory/inventoryDataMixin.js";

export default {
  name: "InventoryDashboard",
  mixins: [inventoryDataMixin],
  props: {
    products: Array,
    disable: Boolean,
    pageSize: {
      type: Number,
      required: false,
      default: 10,
    },
  },

  components: {
    Loader,
  },

  data() {
    return {
      tdClass: null,
      deleteClass: [],
      editClass: [],
      message: null,
      success: false,

      editItemProperty: null,
      editItemPropertyValue: null,
      editItemProductId: null,
      editItemPropertyOrgValue: null,
    };
  },

  computed: {
    items: {
      get() {
        return this.products;
      },
      set(newValue) {
        this.products = newValue;
      },
    },

    loading: {
      get() {
        return this.disable;
      },
      set(newValue) {
        this.disable = newValue;
      },
    },
  },

  created() {
    bus.$on("searchedItems", (data) => {
      this.setItems(data["items"]);
    });
  },

  mounted() {
    Vue.nextTick(function () {
      $('[data-toggle="tooltip"]').tooltip();
    });
  },

  methods: {
    copyProductId(id) {
      let prodID = "#" + id;
      let idToCopy = document.querySelector(prodID);
      idToCopy.setAttribute("type", "text");
      idToCopy.select();

      try {
        let successful = document.execCommand("copy");
        let message = successful ? "Copied!" : "Unable to Copy!";
        alert(message);
      } catch (err) {
        alert("Whoops! unable to copy");
      }

      idToCopy.setAttribute("type", "hidden");
      window.getSelection().removeAllRanges();
    },

    deleteItem(index) {
      if (
        this.user &&
        (this.user.uuid == this.items[index].uploadedBy ||
          this.user.designation == "admin")
      ) {
        if (confirm("You are about to delete " + this.items[index].name)) {
          this.loading = true;
          let url = domain + "item/" + this.items[index].productId;
          axios
            .post(
              url,
              {
                _method: "delete",
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid,
                },
              }
            )
            .then((res) => {
              if (res.data == "done") {
                db.collection("items")
                  .where("productId", "==", this.items[index].productId)
                  .get()
                  .then((query) => {
                    query
                      .forEach((doc) => {
                        doc.ref.delete();

                        this.message = "Deleted successfully";
                        this.success = true;
                        this.loading = false;

                        setTimeout(() => {
                          this.success = false;
                          this.message = null;
                        }, 2000);
                      })
                      .catch((err) => {
                        this.success = true;
                        this.loading = false;

                        setTimeout(() => {
                          this.success = false;
                          this.message = null;
                        }, 2000);
                      });
                    this.items.splice(index, 1);
                  });
              } else {
                alert(res.data);
              }
            });
        }
      } else {
        alert("You are not allowed to delete this");
      }
    },

    editActiveState(index) {
      if (this.user) {
        if (confirm("You are about to change activation of item")) {
          if (
            this.items[index].catActive == "1" &&
            this.items[index].subcatActive == "1" &&
            this.items[index].cocatActive == "1"
          ) {
            this.loading = true;
            let url = domain + "item/" + this.items[index].productId;

            let setActive = 0;
            if (this.items[index].active == 1) {
              setActive = 0;
            } else {
              setActive = 1;
            }

            axios
              .post(
                url,
                {
                  _method: "put",
                  active: setActive,
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": ContentType,
                    "X-Requested-With": XRequestedWith,
                    "X-api-token": firebase.auth().currentUser.uid,
                  },
                }
              )
              .then((res) => {
                if (res.data) {
                  Vue.set(this.items[index], "active", res.data.active);
                }

                this.message = "Updated successfully";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch((err) => {
                this.message = "Some error occured";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
          } else {
            if (this.items[index].catActive != "1") {
              alert(
                "It's category is inactive. So, You can't activate the item."
              );
            } else if (this.items[index].subcatActive != "1") {
              alert(
                "It's subcategory is inactive. So, You can't activate the item."
              );
            } else if (this.items[index].cocatActive != "1") {
              alert(
                "It's cocategory is inactive. So, You can't activate the item."
              );
            }

            Vue.set(this.items[index], active, 0);
          }
        }
      }
    },

    editDisValState(index) {
      if (this.user) {
        if (
          confirm(
            "You are about to change discount visibility of discount value in app"
          )
        ) {
          if (this.user) {
            this.loading = true;
            let url = domain + "item/" + this.items[index].productId;

            axios
              .post(
                url,
                {
                  _method: "put",
                  dis_val: this.items[index].disVal,
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": ContentType,
                    "X-Requested-With": XRequestedWith,
                    "X-api-token": firebase.auth().currentUser.uid,
                  },
                }
              )
              .then((res) => {
                if (res.data) {
                  Vue.set(this.items[index], "disVal", res.data.dis_val);
                }

                this.message = "Updated successfully";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch((err) => {
                this.message = "Some error occured";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
          }
        }
      }
    },

    editProperty(productId, property, value) {
      if (this.user) {
        this.editItemProperty = property;
        this.editItemPropertyValue = value;
        this.editItemPropertyOrgValue = value;
        this.editItemProductId = productId;
      }
    },

    editPropertyEnd(index) {
      if (this.editItemProperty == "cocat") {
        this.editItemPropertyValue = this.cocategories[this.cocategory].itemId;
      }
      if (
        this.user &&
        this.editItemPropertyOrgValue != this.editItemPropertyValue &&
        this.editItemPropertyValue != null &&
        this.editItemPropertyValue != ""
      ) {
        this.loading = true;
        this.message = "Uploading Data, Kripya Shanti banaye rkhe...";
        let url = domain + "item/" + this.editItemProductId;
        if (this.editItemProperty == "name") {
          axios
            .post(
              url,
              {
                _method: "put",
                name: this.editItemPropertyValue,
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid,
                },
              }
            )
            .then((res) => {
              this.items[index].name = res.data.name;
              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch((err) => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else if (this.editItemProperty == "stock") {
          axios
            .post(
              url,
              {
                _method: "put",
                stock: this.editItemPropertyValue,
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid,
                },
              }
            )
            .then((res) => {
              this.items[index].stock = res.data.stock;
              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch((err) => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else if (this.editItemProperty == "qty") {
          axios
            .post(
              url,
              {
                _method: "put",
                qty: this.editItemPropertyValue,
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid,
                },
              }
            )
            .then((res) => {
              this.items[index].qty = res.data.qty;
              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch((err) => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else if (this.editItemProperty == "mrp") {
          axios
            .post(
              url,
              {
                _method: "put",
                MRP: this.editItemPropertyValue,
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid,
                },
              }
            )
            .then((res) => {
              Vue.set(this.items[index], "mrp", res.data.MRP);
              Vue.set(this.items[index], "currentPrice", res.data.currentPrice);
              Vue.set(this.items[index], "discount", res.data.discount);
              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch((err) => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else if (this.editItemProperty == "currentPrice") {
          if (this.editItemPropertyValue <= this.items[index].mrp) {
            axios
              .post(
                url,
                {
                  _method: "put",
                  currentPrice: this.editItemPropertyValue,
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": ContentType,
                    "X-Requested-With": XRequestedWith,
                    "X-api-token": firebase.auth().currentUser.uid,
                  },
                }
              )
              .then((res) => {
                Vue.set(this.items[index], "mrp", res.data.MRP);
                Vue.set(
                  this.items[index],
                  "currentPrice",
                  res.data.currentPrice
                );
                Vue.set(this.items[index], "discount", res.data.discount);
                this.editItemProperty = null;
                this.editItemPropertyValue = null;
                this.editItemProductId = null;
                this.editItemPropertyOrgValue = null;

                this.message = "Data Update Success";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch((err) => {
                //// to show
                this.message = "Some Error Occured!!";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
          } else {
            alert("Your current price is exceeding over MRP");
            this.loading = false;
          }
        } else if (this.editItemProperty == "unit") {
          axios
            .post(
              url,
              {
                _method: "put",
                unit: this.editItemPropertyValue,
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid,
                },
              }
            )
            .then((res) => {
              this.items[index].unit = res.data.unit;
              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch((err) => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else if (this.editItemProperty == "barcode") {
          axios
            .post(
              url,
              {
                _method: "put",
                barcode: this.editItemPropertyValue,
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid,
                },
              }
            )
            .then((res) => {
              this.items[index].barcode = res.data.barcode;
              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch((err) => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else if (this.editItemProperty == "cocat") {
          axios
            .post(
              url,
              {
                _method: "put",
                itemId: this.cocategories[this.cocategory].itemId,
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid,
                },
              }
            )
            .then((res) => {
              this.items[index].itemId = this.cocategories[
                this.cocategory
              ].itemId;
              this.items[index].cocatName = this.cocategories[
                this.cocategory
              ].name;
              this.items[index].subcatName = this.subcategories[
                this.subcategory
              ].name;
              this.items[index].catName = this.subcategories[
                this.category
              ].name;

              this.category = null;
              this.subcategory = null;
              this.cocategory = null;

              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch((err) => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        }
      } else {
        if (this.editItemPropertyOrgValue == this.editItemPropertyValue) {
          this.editItemProperty = null;
          this.editItemPropertyValue = null;
          this.editItemProductId = null;
          this.editItemPropertyOrgValue = null;
        }
      }
    },

    getRowClass(index) {
      if (this.items[index].stock < 5) {
        this.deleteClass[index] = "fa fa-trash modal-trigger text-white";
        this.editClass[index] = "fa fa-pencil-alt text-white";
        return "bg-danger text-white";
      } else {
        this.deleteClass[index] = "fa fa-trash modal-trigger text-red";
        this.editClass[index] = "fa fa-pencil-alt text-red";
        return;
      }
    },

    setItems(items) {
      this.items = items;
    },

    showHideImage(index) {
      if (this.items[index].showImage == 0) {
        Vue.set(this.items[index], "showImage", 1);
      } else {
        Vue.set(this.items[index], "showImage", 0);
      }
    },

    updateSize(size) {
      if (size > 0) {
        this.size = size;

        //updating page counter
        let count = Math.ceil(this.products.length / this.size);
        store.dispatch("patchinventoryPageCount", count);
      }
    },
  },
};
</script>

<style scoped>
.loading {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.inventory-table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 60px;
  height: 80vh;
}

.inventory-table tbody td {
  font-weight: 400;
}

.footer {
  height: 50px;
}

.inventory-th {
  position: sticky;
  background-color: #000000;
  font-size: 14px;
  opacity: 1;
  font-weight: 500;
  z-index: 4;
}

.imageName {
  text-decoration: none;
  color: inherit;
}

.table-items {
  font-size: small;
}
/*
@media (max-width: 768px) {
  th {
    position: sticky;
    top: 50px;
  }
} */
</style>



