 import {
     utcHours,
     utcMins
 } from "@/store/variable";
 import store from "@/store/store";
 import db from "@/components/firebaseInit";
 import firebase from "firebase";

 export const globalMixin = {
     computed: {
         user() {
             return store.state.user;
         }
     },
     methods: {
         goBack() {
             window.history.length > 1 ?
                 this.$router.go(-1) :
                 this.$router.push("/");
         },

         getDateTime(givenDate) {
             if (givenDate) {
                 let dateTime = new Date(givenDate);
                 dateTime.setHours(dateTime.getHours() + utcHours);
                 dateTime.setMinutes(dateTime.getMinutes() + utcMins);
                 let month = dateTime.getMonth() + 1;
                 let date =
                     dateTime.getDate() +
                     "/" +
                     month +
                     "/" +
                     dateTime.getFullYear();
                 var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
                 var hours = dateTime.getHours() % 12 || 12;
                 let time =
                     hours +
                     ":" +
                     dateTime.getMinutes() +
                     ":" +
                     dateTime.getSeconds() +
                     " " +
                     AmOrPm;

                 return [date, time];
             }
         },

         formatDate(date, days) {
             var d = new Date(date);
             d.setDate(d.getDate() + days);
             var month = '' + (d.getMonth() + 1),
                 day = '' + d.getDate(),
                 year = d.getFullYear();


             if (month.length < 2)
                 month = '0' + month;
             if (day.length < 2)
                 day = '0' + day;

             return [year, month, day].join('-');
         }
     },
 }


